body {
    font-family: 'Raleway', sans-serif;
    background-color: #282c34;
}

.Demo {
    min-height: 100vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.Demo section {
    margin-bottom: 5rem;
}

.Demo-pre mark {
    color: #61dafb;
    background: none;
}

.Demo-pre {
    display: inline;
    font-size: 0.85rem;
    float: right;
}

.Demo-input {
    width: 40vw;
    font-size: 2rem;
    padding: .4rem 1rem;
    display: block;
    font-weight: 400;
}
